import NaviPopup from "@/components/NaviPopup.vue";
import DesktopItem from "@/components/DesktopItem.vue";// @ is an alias to /src

@Component({
    components: {
        NaviPopup,
        HelloWorld,
        DesktopItem
    },
    computed: {
        bgGradient: function() {
            return this.$store.state.setting.background.gradient;
        },
        bgColorA: function() {
